<script>
import InfiniteLoading from "vue-infinite-loading";
import axios from "axios";
import VuePrlx from "@/components/product/_directive.js";
const productapi = "http://localhost:81/category/list";

export default {
  components: {
    InfiniteLoading,
  },

  //3자리필터
  filters: {
    comma(val) {
      var num = new Number(val);
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    last2(val) {
      return val.slice(-2);
    },
  },
