    <p>SingleWithProduct</p>
    <router-link
      style="text-decoration: none"
      v-bind:to="`/product/${detailList[0].productDetailNO}`"
    >
      <v-img
        :src="detailList[0].img1"
        :lazy-src="detailList[0].img1"
        style="width: 300px; height: 400px; object-fit: cover"
      />
      <div class="" height="500" width="300">
        <div style="position: relative" class="v-card--reveal transition-fast-in-fast-out">
          <div class="white--text">{{ brand }}</div>
          <div class="white--text">
            {{ name }}
          </div>

          ₩ {{ price | comma }}
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: "Single1",
  components: {},
  data() {
    return {
      bestpd: null,
      cycle: false,
    };
  },
  filters: {
    comma(val) {
      // \B 단어 경계가 아닌 부분에 대응. 여기선 숫자 연속으로 3개 나오는 것에 대응
      // \d 숫자
      // g 글로벌 문자 전체 적용
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  method: {},
  props: ["detailList", "name", "price", "content", "brand"],
  watch: {},
  computed: {},
  created() {},
};
</script>

