<template>
  <div id="app">
    <div v-for="(item, idx) in list" :key="idx" class>
      <div style="heigth: 59px"></div>
      <single-full-product
        v-if="item.detailList.length == 1 && idx % 3 == 0"
        :detailList="item.detailList"
        :name="item.name"
        :price="item.price"
        :content="item.content"
        :brand="item.brand"
      />
      <single-with-product
        v-if="false"
        :detailList="item.detailList"
        :name="item.name"
        :price="item.price"
        :content="item.content"
        :brand="item.brand"
      />
      <single-left-product
        v-if="item.detailList.length == 1 && idx % 3 == 1"
        :detailList="item.detailList"
        :name="item.name"
        :price="item.price"
        :content="item.content"
        :brand="item.brand"
      />
      <single-right-product
        v-if="item.detailList.length == 1 && idx % 3 == 2"
        :detailList="item.detailList"
        :name="item.name"
        :price="item.price"
        :content="item.content"
        :brand="item.brand"
      />
      <double-product
        v-if="item.detailList.length == 2"
        :detailList="item.detailList"
        :name="item.name"
        :price="item.price"
        :content="item.content"
        :brand="item.brand"
      />
      <triple-product
        v-if="item.detailList.length == 3"
        :detailList="item.detailList"
        :name="item.name"
        :price="item.price"
        :content="item.content"
        :brand="item.brand"
      />

      <router-link style="text-decoration: none" v-bind:to="`/product/${item.productDetailNo}`">
        <!-- <div>{{ item }}</div> -->
        <!-- <v-img
            :src="item.img1"
            :lazy-src="item.colorChip"
            style="width: 200px; height: 150px; object-fit: cover"
          /> -->
        <!-- <div text :index="index + 1">
          <span style="color: #041e3a">{{ item.name }} [{{ item.productDetailNo | last2 }}]</span>
        </div>
        <div text>
          <span style="color: #041e3a">{{ item.price | comma }}원</span>
        </div> -->
      </router-link>
    </div>

    <infinite-loading @infinite="infiniteHandler"></infinite-loading>
  </div>
</template>

<script>
import InfiniteLoading from "vue-infinite-loading";
import axios from "axios";
import SingleFullProduct from "@/components/product/megazineTemplate/SingleFullProduct.vue";
import SingleWithProduct from "./megazineTemplate/SingleWithProduct.vue";
import SingleLeftProduct from "./megazineTemplate/SingleLeftProduct.vue";
import SingleRightProduct from "./megazineTemplate/SingleRightProduct.vue";
import DoubleProduct from "./megazineTemplate/doubleProduct.vue";
import TripleProduct from "./megazineTemplate/TripleProduct.vue";

const productapi = "http://localhost:81/productlist/brandlist";

export default {
  components: {
    InfiniteLoading,
    SingleFullProduct,
    SingleWithProduct,
    SingleLeftProduct,
    SingleRightProduct,
    DoubleProduct,
    TripleProduct,
  },

  //3자리필터
  filters: {
    comma(val) {
      var num = new Number(val);
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  data() {
    return {
      singleViewChanger: 0,
      page: 1,
      list: [],
      category: this.$route.params.brand,
    };
  },

  methods: {
    //infiniteHandler는 페이지 끝에 도달햇을 때 작동하는 콜백함수이다.
    infiniteHandler($state) {
      console.log("페이지의 끝에 도달함");
      axios
        .get(productapi, {
          params: {
            category: this.category,
            page: this.page,
          },
        })
        .then(({ data }) => {
          console.log("데이타");
          console.log(data);
          console.log($state);
          console.log("_________");
          if (data.length) {
            this.page += 1;
            // this.list.push(...data.hits);
            this.list.push(...data);
            //Data를 이용하여 다시 랜더링
            $state.loaded();
          } else {
            //랜더링 더이상하지 않음
            $state.complete();
          }
        });
    },
  },
  watch: {
    category(to, from) {
      this.list = [];
    },
    $route(to, from) {
      console.log("to from");
      console.log(to);
      console.log(from);
      console.log(this.$route.params.brand);
      this.category = this.$route.params.brand;
      this.list = [];
      this.page = 1;
      //route를 watch하여 주소 변경시 page, list 를 초기화하고
      //인피니티 스크롤 초기화한다.
      axios
        .get(productapi, {
          params: {
            category: this.category,
            page: 1,
          },
        })
        .then(({ data }) => {
          console.log("데이타");
          console.log(data);
          this.list.push(...data);
          console.log(this.list);
          console.log("_________");
          if (data.length) {
            this.page += 1;
            // this.list.push(...data.hits);

            //Data를 이용하여 다시 랜더링
          }
        });
    },
  },
  created() {},
};
</script>

