<!-- component UI definition-->
<template>
  <div v-if="product.productDetail != null">
    <v-container class="py-0">
      <v-row justify="center" class="mt-1">
        <v-col cols="auto">
          <span class="text-h7 font-weight-bold">{{ $store.state.product.product.productDetail.brand }}</span>
        </v-col>
        <v-spacer></v-spacer>
        <v-col cols="auto">
          <span
            :class="[
              $store.state.product.product.productDetail.discountRate != 0
                ? 'text-decoration-line-through text-h8 font-weight-bold'
                : 'text-h8 font-weight-bold',
            ]"
            >₩ {{ $store.state.product.product.productDetail.price | comma }}</span
          >
        </v-col>
      </v-row>

      <v-row justify="center" class="py-0 mb-3">
        <v-col cols="auto" class="ma-0 py-0">
          <span class="text-h7 font-weight-bold">{{ $store.state.product.product.productDetail.name }}</span>
        </v-col>
        <v-spacer></v-spacer>
        <v-col cols="auto" class="ma-0 py-0">
          <span class="text-h7 red--text" v-if="$store.state.product.product.productDetail.discountRate != 0"
            >₩
            {{
              ($store.state.product.product.productDetail.price * ((100 - $store.state.product.product.productDetail.discountRate) / 100))
                | comma
            }}</span
          >
        </v-col>
      </v-row>
    </v-container>

    <v-carousel
      :continuous="false"
      :show-arrows="true"
      hide-delimiter-background
      delimiter-icon="mdi-minus"
      height="600"
    >
      <v-carousel-item
        v-for="(img, i) in $store.state.product.product.imgList"
        :key="i"
        :src="img"
        reverse-transition="fade-transition"
        fluid
      >
      </v-carousel-item>
    </v-carousel>

    <v-container class="py-0 ml-1">
      <v-row class="mt-1">
        <v-col cols="auto">
          <span class="text-h9">사이즈</span>
        </v-col>
      </v-row>

      <v-container class="d-flex pl-1">
        <v-row class="ma-0 pa-0">
          <v-col cols="auto" v-for="(s, i) in $store.state.product.product.sizeList" :key="i">
            <v-btn-toggle v-model="checkone" @change="sizechange(s)">
              <v-btn small class="float-left" :value="s">
                {{ s }}
              </v-btn>
            </v-btn-toggle>
          </v-col>
        </v-row>
      </v-container>

      <v-row>
        <v-col cols="auto">
          <span class="text-h9">색상</span>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="auto" class="pl-3">
          <div class="ml-3 float-left" v-for="(chip, i) in $store.state.product.product.colorChipList" :key="i">
            <v-container class="float-left d-flex pa-0 ma-0">
              <!-- <v-checkbox
                class="pa-0 ma-0"
                @change="clrchange(chip),detailchange(chip),ChangeColor(chip)"
              >
              </v-checkbox>

              <v-img width="30px" height="30px" :src="chip"> </v-img> -->
              <v-list>
          <v-list-item-group
            class="d-flex justify-center"
            v-model="model"
          >
            <v-list-item :value="chip"
            @change="clrchange(chip),detailchange(chip),ChangeColor(chip)">
              <v-list-item-title>
                <img :src="chip" width="30px" height="30px"/>
              </v-list-item-title>
            </v-list-item>
          </v-list-item-group>
        </v-list>
            </v-container>
          </div>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="auto">
          <span class="text-h9">수량</span>
        </v-col>
      </v-row>

      <v-row class="py-0 pl-3 mb-2">
        <v-col cols="auto" class="ma-0 py-0 d-flex float-left">
          <v-btn class="qty_left" type="button" v-on:click="decreaseSum()" style="width: 20px"
            >-</v-btn
          >
          <input
            type="text"
            name="amount"
            v-model="first"
            class="qty_input"
            style="width: 40px; text-align: center; vertical-align: bottom"
            readonly
            @change="changefirst(first)"
          />

          <v-btn class="qty_right" type="button" v-on:click="increaseSum()" style="width: 20px"
            >+</v-btn
          >
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="auto">
          <span class="text-h9">총 가격</span>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="auto">
          <span class="text-h9 totalPrice" :value="computedSum">₩ {{ computedSum | comma }}</span>
        </v-col>
      </v-row>
    </v-container>

    <v-container class="my-7 absolute">
      <v-row justify="center">
        <v-col cols="auto">
          <v-btn
            elevation="3"
            fab
            rounded
            color="beige navy--text"
            width="160"
            height="50"
            class="text-h5"
            :disabled="isActiveBtn"
            @click="addCart"
          >
            장바구니
          </v-btn>
        </v-col>
        <v-col cols="auto">
          <v-btn
            elevation="3"
            fab
            rounded
            color="navy white--text"
            width="160"
            height="50"
            class="text-h5"
            :disabled="isActiveBtn"
            @click="checkAmount"
          >
            바로구매
          </v-btn>
        </v-col>
      </v-row>
    </v-container>

    <div class="pa-3" style="border-radius: 20px; background-color: #f5f5f5; color: #555555">
      <p class="mb-4">{{ $store.state.product.product.productDetail.content }}</p>
    </div>

    <v-container v-if="$store.state.product.product.withProductList != null">
      <div class="font-weight-bold mt-3 mb-5 ml-3">함께 착용한 상품</div>

      <v-layout>
        <v-carousel
          :continuous="false"
          :show-arrows="true"
          hide-delimiter-background
          delimiter-icon="mdi-minus"
          height="600"
          touchless
        >
          <v-carousel-item
            v-for="(wp, i) in $store.state.product.product.withProductImgList"
            :key="i"
            :src="wp"
            :to="wp | pd"
            @click="move"
            reverse-transition="fade-transition"
            fluid
          >
            <v-container class="v-card--reveal transition-fast-in-fast-out">
              <div class="white--text">
                {{ $store.state.product.product.withProductList.brand }}
              </div>
              <div class="white--text">
                {{ $store.state.product.product.withProductList.name }}
              </div>
              <div
                :class="[
                  $store.state.product.product.withProductList.discountRate != 0
                    ? 'text-decoration-line-through white--text'
                    : 'white--text',
                ]"
              >
                ₩ {{ $store.state.product.product.withProductList.price | comma }}
              </div>
              <div
                class="red--text font-weight-bold"
                v-if="$store.state.product.product.withProductList.discountRate != 0"
              >
                ₩
                {{
                  ($store.state.product.product.withProductList.price *
                    ((100 - $store.state.product.product.withProductList.discountRate) / 100))
                    | comma
                }}
              </div>
            </v-container>
          </v-carousel-item>
        </v-carousel>
      </v-layout>
    </v-container>

    <PurchaseModal v-if="cancelOrderModal">
      <template v-slot:header> <span class="font-weight-bold text-h4">{{title1}}</span></template>
      <template v-slot:body>
        <span class="body-1">{{text1}}</span></template
      >
      <template v-slot:footer>
        <v-container class="my-4">
          <v-row>
              <v-btn  color="beige black--text" class="mr-2 float-right" @click="cancelOrderModal = false"
                >닫기</v-btn
              >
          </v-row>
        </v-container>
      </template>
    </PurchaseModal>
  </div>
</template>

<script>
import apiProduct from "@/apis/product";
import apiOrder from "@/apis/order";
import axios from "axios";
import PurchaseModal from "@/components/order/OrderInputModal.vue";
import store from '@/store/index.js';
export default {
  // component's name found in vue devtools on chrome.
  name: "ProductDetail",
  // components that i want to add
  components: {PurchaseModal},
  // component data definition
  data: function () {
    return {
      model: -1,
      first: 1,
      selected: false,
      count: 1,
      checkone: false,
      product: {},
      cancelOrderModal: false,
      title1: null,
      text1: null,
      sucessmsg: "장바구니에 담겼습니다.",
      title2: "장바구니 안내",
      failmsg:"이미 장바구니에 상품이 있습니다.",
      cnt: null,
      cart: {
        amount: null,
        productDetailNo: null,
        psize: null,
        brand: null,
        productName: null,
        price: null,
        img: null,
        colorChip: null,
        dialog: false,
      },
    };
  },
  filters: {
    comma(val) {
      // \B 단어 경계가 아닌 부분에 대응. 여기선 숫자 연속으로 3개 나오는 것에 대응
      // \d 숫자
      // g 글로벌 문자 전체 적용
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    code(val) {
      var pos = String(val).indexOf("_", 1);
      return String(val).substring(pos + 1, pos + 3);
    },
    pd(val) {
      var pos1 = String(val).indexOf("/", 39);
      var pos2 = String(val).indexOf("_", 39);
      // console.log(String(val).substring(pos1 + 1, pos2 + 3));
      return String(val).substring(pos1 + 1, pos2 + 3);
    },
  },
  // component method definition
  methods: {
    increaseSum: function () {
      this.first = this.first + 1;
      this.cart.amount = this.first;
      return this.first;
    },
    decreaseSum: function () {
      if (this.first > 1) {
        this.first = this.first - 1;
        this.cart.amount = this.first;
        return this.first;
      }
    },
    checkAmount: function() {
      apiProduct.getAmount(this.cart.productDetailNo, this.cart.psize)
      .then((response) => {
          if(this.cart.amount <= response.data){
            var cartList = new Array();
            this.cart.memberId = sessionStorage.getItem("userId");
            // console.log(this.cart);
            cartList.push(this.cart);
            this.$store.dispatch("cart/setCartListByAsync", cartList);
            this.$router.push("/order/orderForm");
          }else if(this.cart.amount > response.data){
            this.cancelOrderModal = true;
            this.title1 = "재고 부족";
            this.text1 = "재고가 부족합니다.";
          }
        })
        .catch((err) => {
          // console.log("수량 에러");
        });
    },
    move: function () {
      this.$router.push(`@/${this.wp | this.pd}`);
    },
    ChangeColor: function(chip){
      var pos = String(chip).indexOf("_", 1);
      var ch = String(chip).substring(pos + 1, pos + 3);
        apiProduct.selectByPdno(`${this.$store.state.product.product.productDetail.productNo}_${ch}`)
        .then(response => {
          this.$store.state.product.product.imgList = response.data.imgList;
          this.$store.state.product.product.productDetail.img1 = response.data.productDetail.img1;
          this.$store.state.product.product.withProductImgList = response.data.withProductImgList;
          this.$store.state.product.product.withProductList = response.data.withProductList;
          this.cart.img = this.$store.state.product.product.imgList[0];
          // console.log("cart img 변경확인 " + this.cart.img);
        })
        .catch(error =>{
            // console.log("changedColor Error");
        });
        // console.log(`${this.$store.state.product.product.productDetail.productNo}_${ch}`);
    },
    sizechange(size) {
      this.cart.psize = size;
    },
    clrchange(chip) {
      this.cart.colorChip = chip;
    },
    addCart() {
      this.cart.memberId = sessionStorage.getItem("userId");
      // console.log(this.cart);
      apiOrder
        .insertCart(this.cart)
        .then((response) => {
          // console.log(response.data);
          this.cancelOrderModal = true;
          this.title1 = this.title2;
          this.text1 = this.sucessmsg;
        })
        .catch((err) => {
          this.cancelOrderModal = true;
          this.title1 = this.title2;
          this.text1 = this.failmsg;
        });
    },
    goOrder() {
      var cartList = new Array();
      this.cart.memberId = sessionStorage.getItem("userId");
      // console.log(this.cart);
      cartList.push(this.cart);
      this.$store.dispatch("cart/setCartListByAsync", cartList);
      this.$router.push("/order/orderForm");
    },
    detailchange(chip) {
      var pos = String(chip).indexOf("_", 1);
      var temp2 =  String(chip).substring(pos + 1, pos + 3);
      this.cart.productDetailNo = this.$store.state.product.product.productDetail.productNo+'_'+temp2;
      // console.log("변경확인"+this.cart.productDetailNo);
    },
  },
  computed: {
    computedSum: function () {
      if (this.$store.state.product.product.productDetail.discountRate > 0) {
        return (
          this.first *
          (this.$store.state.product.product.productDetail.price *
            ((100 - this.$store.state.product.product.productDetail.discountRate) / 100))
        );
      }
      return this.first * this.$store.state.product.product.productDetail.price;
    },
    isActiveBtn() {
      return this.cart.psize === undefined || this.cart.colorChip === undefined;
    },
  },
  created() {
    apiProduct
      .selectByPdno(this.$route.params.pdno)
      .then((response) => {
        this.product = response.data;
        this.$store.state.product.product = response.data;
        // console.log(this.$route.params);
        this.cart.amount = this.first;
        this.cart.psize = this.s;
        this.cart.colorChip = this.chip;
        this.cart.productDetailNo = this.$store.state.product.product.productDetail.productDetailNo;
        this.cart.brand = this.$store.state.product.product.productDetail.brand;
        this.cart.productName = this.$store.state.product.product.productDetail.name;
        this.cart.price =
          this.$store.state.product.product.productDetail.price *
          ((100 - this.$store.state.product.product.productDetail.discountRate) / 100);
        this.cart.img = this.$store.state.product.product.productDetail.img1;
        this.cart.colorChip = this.$store.state.product.product.productDetail.colorChipList;
      })
      .catch((error) => {
        // console.log(error);
        // console.log(this.$route.params);
      });
  },
  mounted() {
  },
  watch: {
      model (val, oldVal) {
        this.val = oldVal
    }
  },
};
</script>

